.login-container {
	width: 42.8%;
	max-width: 700px;
	padding: 1.9rem 6.2rem 2rem 5rem;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: baseline;
}

h1 {
	font-style: normal;
	font-weight: bold;
	font-size: calc(22px + 6 * (100vw / 1440));
	line-height: 34px;
	color: #333333;
	margin-bottom: 0.8rem;
}

.login-heading {
	margin-bottom: 3.7rem;
	> span {
		font-style: normal;
		font-weight: 600;
		line-height: 24px;
		color: #333333;
	}

	a {
		color: #065bea;
		margin-left: 0.3rem;
		&:hover {
			color: #0a4ab6;
		}
	}
}

.info-links {
	> a {
		font-style: normal;
		font-weight: 600;
		color: #75777a;

		&:hover {
			color: #333333;
		}

		&:not(:last-child) {
			margin-right: 1.6rem;
		}
	}
}

@media (max-width: 1200px) {
	.login-container {
		padding: 1.5rem;
	}
}

@media (max-width: 991px) {
	.login-container {
		width: 100%;
	}

	.login-heading {
		margin: 2.5rem 0;
	}
}

@media (max-width: 767px) {
	h1 {
		font-size: calc(22px + (6 + 6 * 0.7) * ((100vw - 360px) / 1440));
	}
}
